import "./Contact.css";
import contact_img from "../../svg/contact-img.svg"

const Contact = () => {
    return ( 
        <section className="sc-contact">
            <div className="container contact-container">
                <div className="row">
                    <div className="col-lg-6">
                        <div className="form-box">
                            <div className="form-title">
                                <h2>  </h2>
                                <p className="primary__text">  <br/>
                                                -!
                                </p>
                            </div>
                            <form>
                                <div className="form-content">
                                    <div className="row">
                                        <div className="col-lg-6 custom__my"><input type="text" placeholder="" required /></div>
                                        <div className="col-lg-6 custom__my"><input type="tel" placeholder="" required /></div>
                                        <div className="col-12 custom__my"><input type="email" placeholder="Email" required /></div>
                                    </div>
                                    <button className="btn primary__btn submit"></button>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="col-lg-6"></div>
                    <div className="contact-img">
                        <img src={contact_img} alt="" />
                    </div>
                </div>
            </div>
        </section>
     );
}
 
export default Contact;